.news {
	&__list {
		// margin-top: 40px;
	}
	&__item {
		border-bottom: 1px solid #c2c2c2;
		margin-bottom: 40px;
		padding-bottom: 40px;
		// border-bottom: 1px solid rgba(0, 0, 0, 0.2);
		// box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
		// border-radius: $border-radius;
		// padding: 50px 30px;
		a {
			font-size: 18px;
			color: $text-color;
			transition: 0.3s;
			&:hover {
				color: $accent-color;
			}
		}
		&-more {
			margin-left: 30px;
		}
		&-title {
			display: block;
			margin-bottom: 30px;
		}
	}
	&-detail {
		margin-top: 40px;
		font-weight: 500;
		&__title {
			font-size: 22px;
			font-weight: bold;
		}
		.news__item-date {
			display: block;
			margin: 30px 0;
			font-weight: bold;
		}
		&__contacts {
			margin-top: 40px;
		}
		&__content {
			padding: 30px 0;
			border-bottom: 1px solid #c2c2c2;
			border-top: 1px solid #c2c2c2;
			ul {
				padding-left: 40px;
				line-height: 40px;
				font-size: 18px;
			}
		}
	}
}
