.footer {
	background: #000;
	&__list {
		display: flex;
		justify-content: space-between;
		padding: 30px 0;
		li {
			color: #fff;
			text-align: center;
			width: 226px;
			@media (max-width: 767px) {
				margin: 0 auto 40px;
			}
		}
		span {
			display: block;
		}
		&-title {
			font-size: 17px;
			position: relative;
			padding-bottom: 20px;
			&:after {
				content: "";
				position: absolute;
				transform: translate(-50%, -50%);
				bottom: 0;
				left: 50%;
				height: 3px;
				width: 50px;
				background-color: $accent-color;
			}
		}
		a {
			margin-top: 12px;
			display: block;
			color: #fff;
			transition: 0.3s;
			&:hover {
				color: $accent-color;
			}
		}
		@media (max-width: 767px) {
			flex-direction: column;
		}
	}
	&__social {
		display: flex;
		gap: 0 20px;
		justify-content: center;
		margin-bottom: 20px;

		i {
			color: #fff;
			font-size: 22px;
			transition: 0.3s;
			&:hover {
				color: $accent-color;
			}
		}
	}
	&__logo {
		img {
			width: 80px;
			border-radius: 50%;
			margin: 0 auto 8px;
			display: block;
		}
	}
	&__copy {
		padding: 30px 0;
		border-top: 1px solid #2e2e2e;
		span {
			color: #fff;
			font-size: 13px;
			display: block;
			text-align: center;
		}
		#year {
			display: inline-block;
		}
		&-text {
			margin-top: 7px;
		}
	}
}
