.shop {
	.hall__item:nth-child(1) .hall__img {
		background-image: url(img/bags-11.jpg);
	}
	.hall__item:nth-child(2) .hall__img {
		background-image: url(img/capa-1.jpg);
	}
	.hall__item:nth-child(3) .hall__img {
		background-image: url(img/shop-acces.jpg);
	}
	.hall__img {
		background-size: contain;
	}
	.hall__item {
		border: 1px solid #000;
	}
	.hall__list-second .hall__item:nth-child(1) .hall__img {
		background-image: url(img/shop-trainer.jpg);
	}
	.hall__list-second .hall__item:nth-child(2) .hall__img {
		background-image: url(img/shop-gloves.jpg);
	}
	.hall__list-second .hall__item:nth-child(3) .hall__img {
		background-image: url(img/shop-clothes.jpg);
	}
	&-card {
		&__list {
			margin-top: 40px;
		}
		&__item {
			margin-bottom: 40px;
			border-bottom: 1px solid rgba(0, 0, 0, 0.2);
			box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
			border-radius: $border-radius;
			display: flex;
			padding: 20px;
			padding: 20px;
			gap: 0 35px;
			@media (max-width: 992px) {
				flex-direction: column;
			}
		}
		&__title {
			font-size: 22px;
			font-weight: bold;
		}
		&__price {
			display: block;
			font-size: 30px;
			color: $accent-color;
			font-weight: bold;
			margin-top: 20px;
		}
		&__text {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			@media (max-width: 992px) {
				margin-top: 30px;
			}
		}
		&__desc-wrap {
			margin: 20px 0;
			span {
				font-size: 16px;
				line-height: 25px;
				color: $text-color;
				margin-bottom: 10px;
				display: inline-block;
				font-weight: 500;
			}
		}
	}
}

.btn-card,
.btn-order-send {
	width: 150px;
	margin: 0 auto;
	&:hover {
		box-shadow: inset 0 0 0 5px $accent-color !important;
		color: #000;
	}
	@media (max-width: 992px) {
		margin-top: 30px;
	}
}

// slider

.rtl-slider-flex {
}
.thumb-prev,
.thumb-next {
	bottom: 12px !important;
	right: calc(9% + 0.75em) !important;
}
.thumb-prev {
	right: calc(0% + 0.75em) !important;
}
/* Necessary styles */
.rtl-slider-flex {
	display: flex;
	flex-direction: row;
	justify-content: center;
	box-sizing: border-box;
	position: relative;
	width: 50%;
	// padding: 0.75em 0.75em 0.5em;
	background: #fff;
	// border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	// box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
	@media (max-width: 992px) {
		flex-direction: column-reverse !important;
		width: 100% !important;
	}
}
.rtl-slider-flex .rtl-slider {
	width: 80%;
	margin-right: 1%;
	@media (max-width: 992px) {
		width: 100% !important;
		margin: 0 auto;
		max-width: 400px;
	}
}
.rtl-slider-flex .rtl-slider-nav {
	width: 19%;
	margin-right: 3px;
	@media (max-width: 992px) {
		width: 52% !important;
	}
}
.rtl-slider-flex .rtl-slider-nav .rtl-slider-slide {
	// padding-top: 90%;
	margin-bottom: 4px;
	// opacity: 0.75;
	transition: all 0.3s ease;
	cursor: pointer;
}
.rtl-slider-flex
	.rtl-slider-nav
	.slick-slide.slick-current.slick-active
	.rtl-slider-slide {
	opacity: 1;
}
// .rtl-slider-slide {
// 	@media (max-width: 992px) {
// 		width: 360px !important;
// 	}
// }
.rtl-slider-flex .thumb-prev,
.rtl-slider-flex .thumb-next {
	position: absolute;
	bottom: 4px;
	right: 0;
	height: 88px;
	width: 9.5%;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	transition: all 0.3s ease;
	background: transparent;
}
.rtl-slider-flex .thumb-prev {
	right: 9.5%;
}
.slick-arrow {
	background: transparent;
	border: none;
}
.rtl-slider-nav .rtl-slider-slide.slick-current {
	border: 1px solid $accent-color;
	border-radius: $border-radius;
}
.rtl-slider-nav {
	@media (max-width: 992px) {
		display: none;
	}
}
// .rtl-slider-nav .slick-list {
// 	height: 206px !important;
// 	@media (max-width: 992px) {
// 		height: 108px !important;
// 	}
// }
.rtl-slider-nav .rtl-slider-slide {
	@media (max-width: 992px) {
		// max-height: 102px !important;
		width: 108px !important;
	}
}
// .rtl-slider-nav img {
// 	@media (max-width: 992px) {
// 		// max-height: 102px !important;
// 		max-width: 120px !important;
// 	}
// }
.slick-arrow {
	position: absolute;
	cursor: pointer;
	color: $text-color;
	// transform: translateY(-50%);
	// top: 50%;
	font-size: 30px;
	transition: 0.3s;
	bottom: 0;
	&:hover {
		color: $accent-color;
	}
	@media (max-width: 992px) {
		font-size: 40px;
		transform: translateY(-50%);
		top: 50%;
		height: 20px;
		color: $accent-color;
	}
}
.slick-prev {
	left: -85px;
	@media (max-width: 992px) {
		left: -20px;
		z-index: 99;
	}
}
.slick-next {
	bottom: 0;
	margin-left: -30px;
	@media (max-width: 992px) {
		right: -20px;
	}
}

// popup-shop

.popup-shop {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.5);
	backdrop-filter: blur(4px);
	pointer-events: none;
	z-index: 999;
	position: fixed;
	visibility: hidden;
	&.active {
		pointer-events: all;
		visibility: visible;
	}
	&__content {
		box-sizing: border-box;
		position: absolute;
		top: 50%;
		transform: translate(-50%, -50%);
		left: 50%;
		background: #fff;
		box-shadow: rgb(0 0 0 / 50%) 0px 0px 15px;
		pointer-events: auto;
		transition: 0.3s;
		display: none;
		padding: 50px 20px 20px;
		max-width: 500px;
		border-radius: $border-radius;
		&.active {
			transition: 0.3s;
			display: block;
		}
		@media (max-width: 767px) {
			width: 100%;
			height: 100%;
			overflow: scroll;
		}
		.form {
			max-width: 100%;
		}
	}
	&__order-wrap {
		display: flex;
		padding-bottom: 20px;
		border-bottom: 1px solid $text-color;
	}
	.form-item input {
		border: solid 1px #c2c2c2;
		color: $text-color;
	}
}
#order-img {
	width: 80px;
	border: 1px solid #000;
	border-radius: $border-radius;
	padding: 3px;
}
#order-title {
	font-size: 18px;
	font-weight: bold;
}
#order-price {
	display: block;
	color: $accent-color;
	font-weight: bold;
	margin-top: 15px;
}
.close-popup-shop {
	position: absolute;
	right: 10px;
	top: 10px;
	cursor: pointer;
	transition: 0.3s;
	i {
		font-size: 25px;
		color: $accent-color;
	}
	&:hover {
		transform: rotate(-90deg);
	}
}
