.main {
	height: 100%;
	position: relative;
	&__hero {
		width: 100%;
		min-height: 650px;
		max-height: 650px;
		background: url(img/main-hero-bg.jpg);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		@media (max-width: 1200px) {
			min-height: 450px;
			max-height: 450px;
		}
		@media (max-width: 767px) {
			min-height: 350px;
			max-height: 350px;
		}
		&-over {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(30, 30, 32, 0.7);
			z-index: 2;
		}
		&-text {
			position: absolute;
			z-index: 9;
			left: 10%;
			top: 30%;
			h1 {
				font-size: 45px;
				color: #fff;
				@media (max-width: 767px) {
					font-size: 30px;
				}
			}
		}
		&-subtitle {
			font-size: 23px;
			color: rgba(255, 255, 255, 0.9);
			display: block;
			padding: 15px 0 35px;
			@media (max-width: 767px) {
				font-size: 17px;
				padding: 10px 0 30px;
			}
		}
	}
	.btn-hero {
		width: 150px;
	}
}

.hall {
	padding: $section-padding;
	img {
		width: 100%;
		max-height: 204px;
	}
	&__list {
		display: flex;
		gap: 20px;
		margin-top: 40px;
		@media (max-width: 767px) {
			flex-direction: column;
		}
	}
	&__item {
		border-radius: $border-radius;
		overflow: hidden;
		perspective: 1px;
		width: 100%;
		transition: 0.7s;
		height: 100%;
		&:nth-child(1) .hall__img {
			background-image: url(img/hall-boxing.jpg);
		}
		&:nth-child(2) .hall__img {
			background-image: url(img/hall-kickboxing.jpg);
		}
		&:nth-child(3) .hall__img {
			background-image: url(img/hall-trx.jpg);
		}
		&:hover .hall__img {
			transform: scale(1.05);
		}
		&:nth-child(1) .coach__img {
			background-image: url(img/coach-1.jpg);
		}
		&:nth-child(2) .coach__img {
			background-image: url(img/coach-2.jpg);
		}
		&:nth-child(3) .coach__img {
			background-image: url(img/coach-3.jpg);
		}
		&:hover .coach__img {
			transform: scale(1.05);
		}
	}
	&__img {
		width: 100%;
		height: 210px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		transition: 0.7s;
		&-wrap {
			overflow: hidden;
		}
	}
	&__text {
		background-color: #000;
		padding: 50px 20px;
		display: block;
	}
	&__title {
		font-size: 18px;
		color: #fff;
		font-weight: 500;
		text-align: center;
		display: block;
		padding-bottom: 20px;
		position: relative;
		&:after {
			content: "";
			position: absolute;
			transform: translate(-50%, -50%);
			bottom: 0;
			left: 50%;
			height: 1px;
			width: 50px;
			background-color: $accent-color;
		}
	}
	&-btn {
		margin-top: 23px !important;
	}
	&__number {
		font-size: 25px;
		color: #fff;
		position: absolute;
		background: $accent-color;
		border-radius: 50%;
		width: 45px;
		height: 45px;
		display: flex;
		align-items: center;
		justify-content: center;
		transform: translate(-50%, -50%);
		left: 50%;
		top: 50%;
		z-index: 9;
	}
	// @media (max-width: 767px) {
	// 	padding: 25px 0;
	// }
}

.our-hall {
	padding: $section-padding;
	img {
		width: 100%;
	}
	&__list {
		display: flex;
		gap: 20px;
		flex-wrap: wrap;
		margin-top: 40px;
	}
	&__item {
		flex: 1 1 341px;
		overflow: hidden;
		height: 270px;
		transition: 0.7s;
		border-radius: $border-radius;
		&:nth-child(1) .our-hall__img {
			background-image: url(img/hall-1.jpg);
		}
		&:nth-child(2) .our-hall__img {
			background-image: url(img/hall-2.jpg);
		}
		&:nth-child(3) .our-hall__img {
			background-image: url(img/hall-3.jpg);
		}
		&:nth-child(4) .our-hall__img {
			background-image: url(img/hall-4.jpg);
		}
		&:nth-child(5) .our-hall__img {
			background-image: url(img/hall-5.jpg);
		}
		&:nth-child(6) .our-hall__img {
			background-image: url(img/hall-6.jpg);
		}
		&:nth-child(7) .our-hall__img {
			background-image: url(img/hall-7.jpg);
		}
		&:nth-child(8) .our-hall__img {
			background-image: url(img/hall-8.jpg);
		}
		&:hover .our-hall__img {
			transform: scale(1.05);
		}
	}

	&__img {
		width: 100%;
		height: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		transition: 0.7s;
	}
	// @media (max-width: 767px) {
	// 	padding: 25px 0;
	// }
}

.coach {
	padding: $section-padding;
	&__img {
		width: 100%;
		height: 300px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		transition: 0.7s;
		&-wrap {
			overflow: hidden;
		}
	}
	&__desc {
		text-align: center;
		color: #fff;
		font-size: 18px;
		display: block;
		margin-top: 23px;
		line-height: 30px;
	}
	.hall__item {
		align-items: stretch;
	}
	.hall__text {
		padding: 50px 20px 40px;
		@media (min-width: 767px) {
			min-height: 305px;
		}
	}
	// @media (max-width: 767px) {
	// 	padding: 25px 0;
	// }
}

.main-form {
	padding: $section-padding;
	width: 100%;
	height: 100%;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	background-image: url(img/main-form-bg.jpg);
	position: relative;
	margin: 50px 0;
	background-attachment: fixed;
	.title-section {
		color: #fff;
	}
	.container {
		position: relative;
		z-index: 9;
	}
	.btn-callback {
		width: 150px;
		display: block;
		margin: 40px auto 0;
	}
	.btn:hover {
		box-shadow: inset 0 0 0 5px $accent-color;
	}
	// @media (max-width: 767px) {
	// 	margin: 25px 0;
	// }
}

.form {
	max-width: 480px;
	margin: 40px auto 0;
	// background: #ffffff69;
	// padding: 40px;
	border-radius: $border-radius;
}
.form-item {
	position: relative;
	margin: 0 auto 25px;
}
#txtArea {
	max-height: 150px;
	min-height: 50px;
	max-width: 480px;
	resize: vertical;
	font-family: inherit;
	font-size: inherit;
}
.form-item input,
#txtArea {
	display: block;
	height: 50px;
	background: 0 0;
	border: solid 1px #fff;
	transition: all 0.3s ease;
	padding: 0 15px;
	max-width: 100%;
	margin: 0 auto;
	width: 100%;
	border-radius: $border-radius;
	color: $text-color;
	font-family: inherit;
	font-size: inherit;
	color: #fff;
}
.form-item input:focus,
#txtArea:focus {
	border-color: #000 !important;
}
.form-item label {
	position: absolute;
	cursor: text;
	z-index: 2;
	left: 10px;
	font-size: 11px;
	top: -7px;
	font-weight: bold;
	border-radius: $border-radius;
	color: #000;
	padding: 0 10px;
	transition: all 0.3s ease;
	background: #fff;
}

.partners {
	padding: $section-padding;
	&__list {
		display: flex;
		align-items: center;
		gap: 20px;
		margin-top: 40px;
		justify-content: space-around;
	}
	img {
		width: 100%;
	}
	// @media (max-width: 767px) {
	// 	margin: 25px 0;
	// }
}
