.header {
	&__top {
		padding: 25px 0;
		&-list {
			display: flex;
			justify-content: space-between;
			a {
				display: flex;
				align-items: center;
				color: $text-color;
				font-weight: bold;
				transition: 0.3s;
				&:hover .header__top-icon {
					box-shadow: inset 0 0 0 2px $accent-color;
				}
				@media (max-width: 992px) {
					flex-direction: column;
					justify-content: center;
					gap: 20px;
				}
			}
			@media (max-width: 992px) {
				flex-direction: column;
				align-items: center;
			}
			li {
				@media (max-width: 992px) {
					margin-bottom: 20px;
				}
			}
		}
		&-icon {
			box-shadow: inset 0 0 0 1px rgb(194, 189, 189);
			width: 45px;
			height: 45px;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: $border-radius;
			margin-right: 15px;
			transition: 0.3s;
			@media (max-width: 992px) {
				margin-right: 0;
			}
		}
	}
	&__nav {
		background: #000;
		padding: 20px 0;
		.container {
			display: flex;
			justify-content: space-between;
			align-items: center;
			@media (max-width: 992px) {
				flex-direction: column;
				justify-content: center;
				gap: 20px;
			}
		}
		nav .nav {
			display: flex;
			a {
				padding: 15px 20px;
				color: #fff;
				text-transform: uppercase;
				font-size: 15px;
				font-weight: bold;
				transition: 0.3s;
				border-radius: $border-radius;
				&:hover {
					color: $accent-color;
					background: none #fff;
				}
			}
			.nav-item {
				border-right: 2px solid rgba(255, 255, 255, 0.2);
				line-height: 29px;
				.header__nav-hover {
					margin-top: 10px;
					a {
						padding: 10px 20px;
					}
				}
			}
			.nav-hover:hover .header__nav-hover {
				// display: block;
				opacity: 1;
				// visibility: visible;
				transform: scale(1);
			}
		}
		&-hover {
			position: absolute;
			background-color: $accent-color;
			flex-direction: column;
			border-radius: $border-radius;
			padding: 10px;
			// margin-top: 7px;
			transition: 0.2s;
			// display: none;
			transform: scale(0);
			opacity: 0;
			// visibility: hidden;
			z-index: 99;
			a {
				display: block;
			}
		}
		.more-icon {
			display: flex;
			background: rgba(255, 255, 255, 0.2);
			justify-content: center;
			align-items: center;
			border-radius: $border-radius;
			color: #fff;
			padding: 5px 10px;
		}
		.more-nav {
			margin-left: 20px;
			position: relative;
			cursor: pointer;
		}
		&-desc {
			@media (max-width: 992px) {
				display: none;
			}
		}
		&-mob {
			box-sizing: border-box;
			position: fixed;
			top: 0;
			bottom: 0;
			right: 0;
			// display: none;
			left: auto;
			right: 0px;
			width: 265px;
			background: #212121;
			box-shadow: rgb(0 0 0 / 50%) 0px 0px 15px;
			transform: translate3d(315px, 0px, 0px);
			pointer-events: auto;
			transition: 0.3s;
			min-height: 100%;
			overflow-y: scroll;
			padding-top: 60px;
			a {
				color: #fff;
				text-transform: uppercase;
				font-size: 15px;
				font-weight: bold;
				display: block;
				padding: 20px;
				transition: 0.3s;
				border-radius: $border-radius;
				&:hover {
					color: $accent-color;
					background: none #fff;
				}
			}
			li {
				border-bottom: 1px solid #2e2c2c;
				:nth-child(1) {
					border-top: 1px solid #2e2c2c;
				}
			}
			&.active {
				transition: 0.3s;

				transform: translate3d(0, 0, 0);
			}
		}
		&-dropdown {
			display: none;
			a {
				padding-left: 50px;
				&:nth-child(1) {
					border-top: 1px solid #2e2c2c;
				}
			}
		}
	}
	&__logo img {
		width: 80px;
		border-radius: 50%;
	}
	&__social {
		display: flex;
		gap: 0 20px;

		i {
			color: #fff;
			font-size: 22px;
			transition: 0.3s;
			&:hover {
				color: $accent-color;
			}
		}
	}
}

.popup-nav {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.5);
	transform: translate3d(0, 0, 0);
	backdrop-filter: blur(4px);
	pointer-events: auto;
	visibility: hidden;
	// display: none;
	pointer-events: none;
	// transition: 0.5s;
	z-index: 999;
	&.active {
		pointer-events: all;
		visibility: visible;
	}
}
.btn-burger {
	opacity: 0;
	visibility: hidden;
	background: #212121;
	width: 50px;
	height: 50px;
	border-radius: $border-radius;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	cursor: pointer;
	position: fixed;
	top: 15px;
	right: 15px;
	z-index: 9;
	div {
		width: 30px;
		height: 3px;
		background-color: #fff;
		margin-bottom: 6px;
		&:nth-child(3) {
			margin-bottom: 0;
		}
	}
	@media (max-width: 992px) {
		visibility: visible;
		opacity: 1;
	}
}
.close-nav-mob {
	background: #fff;
	width: 45px;
	height: 45px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	right: 0;
	top: 0;
	border-top-left-radius: $border-radius;
	border-bottom-left-radius: $border-radius;
	cursor: pointer;
	i {
		font-size: 25px;
		color: $accent-color;
	}
}
.nav-dropdown {
	position: relative;
	i {
		position: absolute;
		top: 23px;
		color: #fff;
		font-size: 16px;
		right: 20px;
	}

	&.active > .fa-angle-down {
		transform: rotate(180deg);
	}
}
.nav-dropdown:hover .fa-angle-down {
	color: #000;
}
