.contacts {
	font-size: 18px;
	&__wrap {
		display: flex;
		gap: 40px;
		@media (max-width: 992px) {
			flex-direction: column;
		}
	}
	ul {
		li {
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		a {
			margin-top: 15px;
			display: block;
		}
	}
	&__map {
		width: 100%;
	}
	&__text {
		width: 100%;
	}
	span {
		display: block;
	}
	&__hall-img {
		max-width: 300px;
		margin-top: 25px;
		@media (max-width: 480px) {
			max-width: 100%;
		}
	}
	&__social {
		margin-top: 20px;
		i {
			color: $text-color;
			font-size: 30px;
			transition: 0.3s;
			margin-right: 15px;
			&:hover {
				color: $accent-color;
			}
		}
	}
	iframe {
		border-radius: $border-radius;
	}
}
