.about {
	padding: $section-padding;
	// @media (max-width: 767px) {
	// 	padding: 25px 0;
	// }
	img {
		width: 100%;
		border-radius: $border-radius;
	}
	&__img-fix img {
		width: 400px;
		@media (max-width: 767px) {
			width: 100%;
		}
	}
	&__wrap {
		margin-top: 80px;
		@media (max-width: 767px) {
			margin-top: 50px;
		}
	}
	&__item {
		display: flex;
		gap: 80px;
		margin-top: 60px;
		justify-content: center;
		&:nth-child(1) {
			@media (max-width: 767px) {
				flex-direction: column-reverse;
			}
		}
		@media (max-width: 767px) {
			flex-direction: column;
			margin-top: 20px;
			gap: 20px;
		}
		&.reverse {
			@media (max-width: 767px) {
				flex-direction: column-reverse;
			}
		}
	}
	&__text-wrap {
		max-width: 725px;
		font-size: 18px;
		line-height: 40px;
	}
	p {
		margin-top: 30px;
		display: block;
		font-size: 18px;
		line-height: 40px;
		&:nth-child(1) {
			margin-top: 0;
		}
		@media (max-width: 767px) {
			margin-top: 20px;
		}
	}
	&__title {
		margin-bottom: 40px;
		display: block;
		@media (max-width: 767px) {
			margin-bottom: 20px;
		}
	}
	&.kickboxing {
		ul.list {
			padding-left: 40px;
			li {
				font-size: 18px;
				line-height: 40px;
				&:before {
					content: "â€”";
					position: relative;
					left: -5px;
				}
			}
		}
		.block {
			display: flex;
			margin-top: 40px;
			img {
				width: auto;
			}
			p {
				margin-top: 15px !important;
				line-height: 30px !important;
				&:nth-child(1) {
					margin-top: 0 !important;
				}
			}
			&-img {
				margin-right: 30px;
			}
		}
	}
}
