.media {
	&__list {
		display: flex;
		gap: 40px 20px;
		flex-wrap: wrap;
		margin-top: 40px;
	}
	&__item {
		flex: 1 1 370px;
		&:hover .media__img-wrap img {
			transform: scale(1.1);
		}
		&:hover .link {
			text-decoration: none;
		}
		.link {
			margin-top: 15px;
			display: block;
			font-size: 18px;
		}
	}
	&__img-wrap {
		overflow: hidden;
		border-radius: $border-radius;
		max-height: 315px;
		img {
			transition: 0.7s;
		}
	}
	&.club .media__list {
		gap: 20px;
	}
	&.club .media__img-wrap {
		height: 315px;
		max-width: 545px;
		@media (max-width: 992px) {
			max-width: 100%;
			height: auto;
		}
	}
	// &.club .media__item {
	// 	flex: 1 1 260px;
	// }
}
