.popup-shop {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  pointer-events: none;
  z-index: 999;
  position: fixed;
  visibility: hidden;
}
.popup-shop.active {
  pointer-events: all;
  visibility: visible;
}
.popup-shop__content {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 15px;
  pointer-events: auto;
  transition: 0.3s;
  display: none;
  padding: 50px 20px 20px;
  max-width: 500px;
  border-radius: 5px;
}
.popup-shop__content.active {
  transition: 0.3s;
  display: block;
}
