@import "variables";
@import "preloader";
@import "header";
@import "main";
@import "about";
@import "news";
@import "contacts";
@import "media";
@import "shop";
@import "footer";

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

*,
*:focus {
	outline: none;
}

ul li {
	list-style: none;
}

a {
	text-decoration: none;
}

body {
	width: 100%;
	min-height: 100vh;
	overflow-x: hidden;
	font-family: "Montserrat", sans-serif;
	letter-spacing: 0.3px;
	color: $text-color;
	&.hidden {
		overflow: hidden;
	}
}
iframe,
video {
	width: 100%;
}
.font-bold {
	font-weight: bold;
}

.container {
	max-width: 1140px;
	margin: 0 auto;
	padding: 0 15px;
}
.desc {
	@media (max-width: 767px) {
		display: none;
	}
}
.mob {
	display: none;
	@media (max-width: 767px) {
		display: block;
	}
}
.link {
	color: $accent-color !important;
	font-weight: bold;
	text-decoration: underline;
	transition: 0.3s;
	&:hover {
		text-decoration: none;
	}
}
.line {
	width: 100%;
	height: 1px;
	margin: 30px 0;
	background-color: #c2c2c2;
}
.mb10 {
	margin-bottom: 10px;
	display: block;
}
.back {
	display: block;
	margin-top: 60px;
	i {
		margin-right: 15px;
	}
	@media (max-width: 767px) {
		margin-top: 40px;
	}
}
.shedule-desc,
.shedule-title {
	display: block;
	@media (min-width: 767px) {
		text-align: center;
	}
}
.table {
	margin: 30px auto;
	overflow: auto;
	table {
		margin: auto;
	}
	td {
		padding: 12px;
	}
}
.btn {
	color: #fff;
	border-radius: $border-radius;
	border: none;
	background-color: $accent-color;
	padding: 15px 0;
	cursor: pointer;
	display: block;
	font-size: 20px;
	transition: 0.3s;
	transform: translateZ(0);
	&:hover {
		box-shadow: inset 0 0 0 5px #fff;
		background: none;
	}
	@media (max-width: 767px) {
		font-size: 18px;
		padding: 13px 0;
	}
}
.btn-primary {
	color: #fff;
	border-radius: $border-radius;
	border: none;
	box-shadow: inset 0 0 0 5px $accent-color;
	padding: 15px 0;
	cursor: pointer;
	display: block;
	font-size: 20px;
	transition: 0.3s;
	transform: translateZ(0);
	background: none;
	&:hover {
		background-color: $accent-color;
		color: #fff;
	}
	@media (max-width: 767px) {
		font-size: 18px;
		padding: 13px 0;
	}
}
.btn-small {
	font-size: 18px;
	padding: 13px 0;
	width: 150px;
	display: block;
	margin: auto;
}

.title-section {
	color: $text-color;
	font-size: 45px;
	font-weight: 500;
	text-align: center;
	padding-bottom: 15px;
	position: relative;
	&:after {
		content: "";
		position: absolute;
		transform: translate(-50%, -50%);
		bottom: 0;
		left: 50%;
		height: 3px;
		width: 50px;
		background-color: $accent-color;
	}
	@media (max-width: 767px) {
		font-size: 30px;
	}
}
